.manage {
  background: rgb(244, 245, 249);
  display: flex;
  &__content {
    width: calc(100% - 70px);
    @include r(767) {
      width: calc(100% - 40px); } }




  &__aside {
    min-height: 100vh;
    box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
    background-color: $charcoal-grey;
    @include center-column;
    justify-content: flex-start;
    padding: 20px;
    @include r(767) {
      padding: 10px 5px; }
    &__logo {
      margin-bottom: 50px; } }





  &__header {
    box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
    background-color: $white;
    padding: 25px 0;
    @include r(767) {
      padding: 10px 0; }
    &__content {
      @include center-y;
      justify-content: flex-end;
      @include r(767) {
        flex-wrap: wrap;
        justify-content: center; } }
    p {
      font-size: 12px;
      font-weight: normal;
      color: rgba(41, 49, 52, 0.8);
      margin-right: 35px;
      @include r(767) {
        width: 100%;
        text-align: center;
        margin-right: 0;
        margin-bottom: 10px; } }
    ul {
      display: none;
      position: absolute;
      left: -90px;
      width: 200px;
      top: 33px;
      border-radius: 10px;
      box-shadow: 0px 1px 64px 0 rgba(0, 0, 0, 0.24);
      background-color: $white;
      padding: 8px 0;
      z-index: 10;
      &:before {
        content: '';
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        top: -15px;
        border: 8px solid transparent;
        border-bottom: 8px solid $white; }
      a {
        padding: 8px 15px;
        display: block;
        color: $dark;
        font-size: 12px;
        &:hover {
          background: rgba($very-light-pink, 0.2); } } }
    &__notifications {
      @include center;
      margin-right: 23px;
      cursor: pointer;
      position: relative;
      &:hover {
        i {
          color: $bright-light-blue; } }
      i {
        color: $dark;
        font-size: 20px;
        position: relative;
        @include tr;
        span {
          position: absolute;
          right: 0;
          width: 10px;
          height: 10px;
          border-radius: 100%;
          border: solid 1.5px $white;
          background-color: $strawberry; } }
      b {
        margin-left: 6px;
        font-size: 11px;
        font-weight: bold;
        color: $strawberry; } }
    &__user {
      cursor: pointer;
      position: relative;
      &:hover {
        span, i {
          color: $bright-light-blue; } }
      span {
        font-size: 12px;
        font-weight: 600;
        color: $dark;
        @include tr; }
      i {
        font-size: 7px;
        padding-left: 5px;
        @include tr; }
      ul {
        width: 100px;
        left: 50%;
        transform: translate(-50%);
        @include r(1250) {
          left: calc(50% - 10px); } } } }




  &__title {
    display: flex;
    justify-content: space-between;
    padding-top: 35px;
    @include r(767) {
      flex-direction: column;
      align-items: center;
      padding-top: 20px; }
    h1 {
      font-size: 30px;
      font-weight: normal;
      color: $dark;
      @include r(767) {
        padding-bottom: 15px;
        font-size: 25px; } }
    a {
      @include r(767) {
        margin-bottom: 15px; }
      i {
        margin-right: 12px; } } }






  &__statistics {
    padding: 30px 0;
    &__block {
      border-radius: 10px;
      box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
      background-color: $white;
      padding: 25px 30px;
      @include r(1200) {
        padding: 25px 20px; }
      @include center-y;
      min-height: 106px;
      margin-bottom: 15px;
      &__img {
        margin-right: 25px; }
      &__text {
        display: flex;
        flex-direction: column;
        p {
          font-size: 14px;
          font-weight: 600;
          color: $dark;
          margin-bottom: 5px; }
        span {
          font-size: 30px;
          font-weight: bold;
          color: $charcoal-grey; } } } }




  &__table {
    border-radius: 10px;
    box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
    background-color: $white;
    margin-bottom: 100px;
    @include r(1250) {
      overflow-x: auto; }
    @include r(767) {
      margin-bottom: 30px; }
    &__content {
      min-width: 1100px;
      display: block;
      @include r(767) {
        min-width: 900px; } }
    table, tbody, thead {
      width: 100%;
      display: block; }
    tr {
      display: flex;
      align-items: center;
      padding: 0 10px;
      border-bottom: 1px solid $very-light-pink;
      @include tr;
      &:hover {
        background: rgba($very-light-pink, 0.2); } }
    thead tr:hover {
      background: none; }
    tbody tr:last-child {
      border-bottom: none; }
    td, th {
      display: flex;
      &:first-child {
        width: 6%; }
      &:nth-child(2) {
        width: 16%; }
      &:nth-child(3) {
        width: 15%; }
      &:nth-child(4) {
        width: 11%; }
      &:nth-child(5) {
        width: 8%; }
      &:nth-child(6) {
        width: 11%; }
      &:nth-child(7) {
        width: 18%; }
      &:nth-child(8) {
        width: 10%; }
      &:last-child {
        width: 5%; } }
    th {
      padding: 25px 12px;
      display: flex;
      align-items: center;
      a {
        &:hover {
          i {
            color: $bright-light-blue; } } }
      i {
        font-size: 12px;
        color: $dark;
        @include tr; }
      span {
        font-size: 12px;
        font-weight: bold;
        color: $dark;
        text-transform: uppercase;
        margin-right: 10px;
        @include r(767) {
          font-size: 10px; } }
      input {
        background: none;
        @include placeholder-color($dark);
        font-size: 12px;
        font-weight: bold;
        text-transform: uppercase;
        color: rgba(19, 28, 46, 0.4);
        padding-left: 8px; } }
    .table__datepicker {
      position: relative;
      input {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0; } }
    .table__gallery {
      display: flex;
      a {
        margin-right: 5px;
        position: relative;
        @include center;
        cursor: default;
        &:last-child {
          cursor: pointer; }
        img {
          @include r(767) {
            width: 45px;
            height: 45px; } }
        i {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          @include center;
          color: $white;
          background: rgba($charcoal-grey, 0.3);
          font-size: 22px;
          @include tr; }
        &:hover {
          i {
            color: $bright-light-blue;
            background: rgba($charcoal-grey, 0.5); } } } }
    .table__more {
      i {
        font-size: 24px;
        color: $dark;
        @include tr; }
      &:hover {
        i {
          color: $bright-light-blue; } } }
    td {
      padding: 25px 12px;
      text-align: left;
      position: relative;
      p {
        font-size: 14px;
        font-weight: normal;
        color: $dark;
        @include r(767) {
          font-size: 11px; } }
      ul {
        display: none;
        position: absolute;
        left: calc(50% - 3px);
        top: 30%;
        transform: translate(-50%, 50%);
        width: 105px;
        border-radius: 7px;
        box-shadow: 0px 1px 64px 0 rgba(0, 0, 0, 0.24);
        background-color: $white;
        padding: 5px 0;
        z-index: 10;
        @include r(1250) {
          left: calc(50% - 15px); }
        &:before {
          content: '';
          position: absolute;
          left: 50%;
          transform: translate(-50%);
          top: -15px;
          border: 8px solid transparent;
          border-bottom: 8px solid $white;
          @include r(1250) {
            left: calc(50% + 12px); } }
        a {
          padding: 4px 13px;
          display: flex;
          align-items: center;
          font-size: 12px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: 2.08;
          letter-spacing: normal;
          text-align: left;
          color: $dark;
          i {
            @include center;
            width: 20px;
            height: 20px;
            color: $bright-light-blue;
            border-radius: 100%;
            border: 1px solid $dark;
            font-size: 9px;
            margin-right: 10px;
            @include tr; }
          // &:hover
 }          //   background: rgba($very-light-pink, 0.2)
        li:first-child {
          a:hover {
            background: $fresh-green;
            color: $white;
            i {
              color: $white;
              border-color: $white; } } }
        li:last-child {
          a:hover {
            background: $strawberry;
            color: $white;
            i {
              color: $white;
              border-color: $white; } } } } }
    tr.open {
      p {
        font-weight: 600; } } } }
